.header{
    height: 100vh;
    width: 105px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1001;
    background-color: #2E4053;
    opacity: 1;
    transition: all .5s ease-out;
    overflow: hidden;
    &:hover{
        width: 200px;
    }
}

.logo{
    &-container img{
        margin-top: 20px;
        margin-left: 8px;
        width: 80px;
    }
    &-title{
        color: white;
        font-size: 20px;
        margin: 10px 0 0 10px;
        margin-top: 10px;
        visibility: hidden;
    }
}

.header-menu{
    list-style: none;
    color: white;
    font-size: 19px;
    margin: 0;
    padding: 0;
    margin-top: 40px;
    position: relative;
    & li{
        padding: 7px 2px 7px 10px;
        margin: 20px 0;
        
        &:hover{
            background-color: #34495E;
            margin-left: 4px;
        }
    }
}
.copyright{
    position: absolute;
    bottom: 10px;
    left: 10px;
    color: #95A5A6;
}
.icons{
    font-size: 30px;
    margin-left: 20px;
    color: white;
}
.header-items{
    text-decoration: none;
    color: white;
    font-size: 19px;
    margin-left: 10px;
    padding: 0;
    position: absolute;
    left: 64px;
    opacity: 0;
    transform: translate(100px, 8px);
    transition: all .8s;
    cursor: pointer;
    
}
.header:hover .header-items{
 
    transform: translate(0px, 8px);
    opacity: 1;
}


.slow-1{
    transition-delay: .1s;
}
.slow-2{
    transition-delay: .2s;
}
.slow-3{
    transition-delay: .3s;
}
.slow-4{
    transition-delay: .4s;
}